import React, { useState } from "react"
import { QRCode } from "react-qr-svg"
import "./app.css"

const defaultItemsText = `
🏠客厅🏠 存包处
缝纫室 拉链纽扣 游标卡尺	拉链纽扣 缝纫机器
实验室 打印耗材	
实验室 列印废料	打印备件	强电线缆 列印角料	厨房备件 收纳工具 缝纫角料	缝纫备件
手工桌 胶刀尺笔 缝纫线材	打磨流程 台钳钻架	手持电钻 小缝纫机
厨房	米面干粮 储备水源	厨房纸巾 储备湿粮	烘培耗材 蛋打发机 微蒸烤箱	储备汤料	
仓库	排插线板 储备厨纸	 硬纸板箱	单车配件 泡沫盒子
卫生妆间	洗脸巾  储备厕纸
冲淋浴室	洗衣机
`.trim()

export function Item({ value }) {
  return (
    <li className="item">
      <QRCode value={value || ""} className="qrcode" />
      <h2>{value}</h2>
    </li>
  )
}
export function Items({ items }) {
  return (
    <ul className="items">
      {items.map((e, i) => (
        <Item value={e} key={e + i} />
      ))}
    </ul>
  )
}
export default function App() {
  const [state, setState] = useState({ itemsText: defaultItemsText })
  const change = e => {
    setState({ itemsText: e.currentTarget.value })
  }
  return (
    <div className="root">
      <div className="noprint control">
        <h1>雪星仓储管理系统：标签生成器</h1>
        <h2>使用方法</h2>
        <ol>
          <li>在编辑框写好相应物品的，用空格或换行分割</li>
          <li>
            按 Ctrl+P 将下方标签打印到背胶纸上，你可以调整到你喜欢的 Scale。
          </li>
          <li>按需用透明胶或者热敏覆膜把打印表面保护起来</li>
          <li>裁剪</li>
          <li>贴在你需要的地方，比如各种物品或盒子表面</li>
        </ol>
        <textarea onChange={change} value={state.itemsText} />
      </div>
      <Items items={state.itemsText.trim().split(/\s+/)} />
    </div>
  )
}
